import React, { Fragment, useState, useEffect } from 'react';
import { Card, CardHeader, CardBody } from 'reactstrap';
import { NotificationManager } from 'components/common/react-notifications';
import StatusUpdate from 'components/UpdateStatus';
import Rating from 'components/common/Rating';
import ReactLoading from 'components/Loading';
import ImagePreView from 'components/PerviewImage/ModalView';
import DeleteData from 'components/DeleteData';
import { convertDate, convertDateTime } from 'constants/defaultValues';
import { getProviderInfo } from 'Apis/admin';
import { tabs } from './constants';
const ProviderDetails = (props) => {
	const [userDetails, setUserDetails] = useState({
		...props.location.state.post,
	});
	const [totalBookings, setTotalBookings] = useState([]);
	const [rating, setRating] = useState(0);
	const [imagePath, setImagePath] = useState('');
	const [viewImage, setViewImage] = useState(false);
	const [loading, setLoading] = useState(false);
	const [activeTab, setActiveTab] = useState(tabs[0].key);
	const [providerServices, setProviderServices] = useState([]);
	const DeleteDataLocal = (index) => {
		const booking = [...totalBookings];
		booking.slice(index, 1);
		setTotalBookings(booking);
	};
	useEffect(() => {
		if (userDetails.id) {
			setLoading(true);
			getProviderInfo(userDetails.id)
				.then((res) => {
					const { data } = res;
					const { rating, allServices, bookings } = data.data;
					setLoading(false);
					setProviderServices(allServices);
					setRating(rating);
					setTotalBookings(bookings);
				})
				.catch((err) => {
					setLoading(false);
					if (err.response) {
						const { data } = err.response;
						NotificationManager.warning(
							data.error_message,
							'Something went wrong',
							3000,
							null,
							null,
							''
						);
					}
				});
		}
	}, [userDetails.id]);
	return (
		<Fragment>
			<Card>
				<CardHeader>
					<h1 style={{ paddingTop: '31px' }}> Provider Details </h1>
				</CardHeader>
			</Card>
			<ul className='nav nav-tabs mb-5 mt-4'>
				{tabs.map(({ name, key }) => (
					<li className='nav-item' key={key} onClick={() => setActiveTab(key)}>
						<span
							className={`nav-link ${activeTab === key ? 'active' : ''}`}
							href='#'
						>
							{name}
						</span>
					</li>
				))}
			</ul>

			<ReactLoading loading={loading} />
			{activeTab === 'usersDetails' && (
				<>
					<Card className='mb-5 pb-5'>
						<CardBody>
							<div>
								<b> Name </b>: {userDetails.name}
							</div>
							<hr />
							<div>
								<b> Email </b>: {userDetails.email}
							</div>
							<hr />
							<div>
								<b> Phone </b>: {userDetails.phone}
							</div>
							<hr />

							<div>
								<b> Location </b>: {userDetails.address}
							</div>
							<hr />

							<div>
								<b> DOB </b>: {convertDate(userDetails.dob)}
							</div>
							<hr />

							<div className='d-flex'>
								<b className='mr-3'> Rating: </b>{' '}
								<Rating interactive={false} total={5} rating={rating} />
							</div>
							<hr />

							<div>
								<b> Document verify </b> : - &nbsp;
								<StatusUpdate
									statusMessage={{
										0: 'Not Approved (click to Approve)',
										1: 'Approved',
									}}
									table='users'
									onUpdate={(data) =>
										setUserDetails({ ...userDetails, ...data })
									}
									data={userDetails}
									updateKey='isDocumentVerify'
									isButton
								/>
							</div>

							<hr />
							<div>
								<b> Provider Status </b> : - &nbsp;
								<StatusUpdate
									statusMessage={{
										0: 'Inactive',
										1: 'Active',
									}}
									table='users'
									onUpdate={(data) =>
										setUserDetails({ ...userDetails, ...data })
									}
									data={userDetails}
									updateKey='status'
									isButton
								/>
							</div>

							<hr />
							<div>
								<b> Profile </b> :{' '}
								<img
									onClick={() => {
										setImagePath(userDetails.profile);
										setViewImage(true);
									}}
									alt={userDetails.first_name}
									src={userDetails.profile}
									className='list-thumbnail responsive border-0 card-img-left'
								/>
							</div>
						</CardBody>
					</Card>
				</>
			)}
			{activeTab === 'documents' && (
				<>
					<Card className='mb-5'>
						<CardHeader>
							<div className='d-flex justify-content-between align-items-center'>
								<h3 className='mt-3'>Provider Documents</h3>
								<StatusUpdate
									statusMessage={{
										0: 'Not Approved (click to Approve)',
										1: 'Approved',
									}}
									table='users'
									onUpdate={(data) =>
										setUserDetails({ ...userDetails, ...data })
									}
									data={userDetails}
									updateKey='isDocumentVerify'
									isButton
								/>
							</div>
						</CardHeader>
						<CardBody>
							<table className='table table-striped'>
								<thead>
									<tr>
										<th>#</th>
										<th>Name</th>
										<th>Doucment</th>
									</tr>
								</thead>
								<tbody>
									<tr>
										<td>{1}</td>
										<td>Primary ID</td>
										<td>
											{' '}
											<img
												onClick={() => {
													setImagePath(
														userDetails.primaryId ||
															'/public/assets/img/logo.png'
													);
													setViewImage(true);
												}}
												alt={userDetails.primaryId}
												src={
													userDetails.primaryId || '/public/assets/img/logo.png'
												}
												className='list-thumbnail responsive border-0 card-img-left'
											/>
										</td>
									</tr>
									<tr>
										<td>{2}</td>
										<td>NBI</td>
										<td>
											{' '}
											<img
												onClick={() => {
													setImagePath(
														userDetails.nbi || '/public/assets/img/logo.png'
													);
													setViewImage(true);
												}}
												alt={userDetails.nbi}
												src={userDetails.nbi || '/public/assets/img/logo.png'}
												className='list-thumbnail responsive border-0 card-img-left'
											/>
										</td>
									</tr>
								</tbody>
							</table>
						</CardBody>
					</Card>
				</>
			)}
			{activeTab === 'services' && (
				<>
					<Card className='mb-5'>
						<CardHeader>
							<div className='d-flex justify-content-between align-items-center'>
								<h3 className='mt-3'>Provider Services</h3>
							</div>
						</CardHeader>
						<CardBody>
							<table className='table table-striped'>
								<thead>
									<tr>
										<th>#</th>
										<th>Name</th>
										<th>Image</th>
										<th>Price</th>
										<th>Created Date</th>
									</tr>
								</thead>
								<tbody>
									{providerServices.map(
										({ id, name, image = '', price, created }, key) => (
											<tr key={id}>
												<td>{key + 1}</td>
												<td>{name}</td>
												<td>
													<img
														onClick={() => {
															setImagePath(
																image || '/public/assets/img/logo.png'
															);
															setViewImage(true);
														}}
														alt={image}
														src={image || '/public/assets/img/logo.png'}
														className='list-thumbnail responsive border-0 card-img-left'
													/>
												</td>
												<td>{price}</td>
												<td>{convertDate(created)}</td>
											</tr>
										)
									)}
									{providerServices.length === 0 && (
										<tr className=''>
											<td colSpan='6'>
												<h2 className='bg-red'>No record Found</h2>
											</td>
										</tr>
									)}
								</tbody>
							</table>
						</CardBody>
					</Card>
				</>
			)}
			{activeTab === 'bookings' && (
				<>
					<Card>
						<CardHeader>
							{' '}
							<h3 className='mt-3'>Provider Bookings</h3>
						</CardHeader>
					</Card>
					<table className='table table-striped'>
						<thead>
							<tr>
								<th>#</th>
								<th>User Name</th>
								<th>User Email</th>
								<th>Service Name</th>
								<th>Price</th>
								<th>Start/End Time</th>
								<th>Booking Date</th>
								<th>Action</th>
							</tr>
						</thead>
						<tbody>
							{totalBookings.map((booking, key) => (
								<tr key={booking.id}>
									<td>{key + 1}</td>
									<td>{booking.userName}</td>
									<td>{booking.userEmail}</td>
									<td>{booking.serviceDetails?.name}</td>
									<td>{booking.totalPrice}</td>
									<td>
										{convertDateTime(booking.bookingStartTime)} /{' '}
										{convertDateTime(booking.bookingEndTime)}
									</td>
									<td>{convertDate(booking.bookingDate)}</td>
									<td>
										{!booking.isComplete ? (
											<DeleteData
												view='Booking'
												classes='btn-sm'
												table='booking'
												isInput
												data={booking.id}
												ondelete={() => DeleteDataLocal(key)}
											>
												Cancel
											</DeleteData>
										) : (
											<span className='badge badge-pill badge-success'>
												Completed
											</span>
										)}
									</td>
								</tr>
							))}
							{totalBookings.length === 0 && (
								<tr className=''>
									<td colSpan='8'>
										<h2 className='bg-red'>No record Found</h2>
									</td>
								</tr>
							)}
						</tbody>
					</table>
				</>
			)}
			<span pb-5 />
			<ImagePreView
				imagePath={imagePath}
				showModel={viewImage}
				onClose={(value) => setViewImage(value)}
			/>
		</Fragment>
	);
};

export default ProviderDetails;
